<template>
  <!--  -->
  <div class="modal-overlay" @click.self="closeModal">
    <transition name="fade-scale">
      <div class="modal-container">
        <h2></h2>
        <p> PDF / DOC / DOCX / PNG </p>

        <div class="modal-body">
          <!-- / -->
          <div class="upload-section">
            <div
              class="drop-zone"
              @dragover.prevent="onDragOver"
              @dragleave.prevent="onDragLeave"
              @drop.prevent="onDropFiles"
              @click="triggerFileInput"
            >
              <!--  file input ref="fileInput"  -->
              <input
                type="file"
                accept=".pdf,.doc,.docx,.png"
                id="fileInput"
                ref="fileInput"
                style="display: none"
                @change="onFileChange"
              />
              <label for="fileInput">
                <span v-if="!selectedFile"></span>
                <span v-else></span>
              </label>
            </div>
          </div>

          <!--  -->
          <div class="preview-section">
            <div v-if="selectedFile" class="preview-container">
              <div class="preview-header">
                <p class="preview-file-name">{{ selectedFile.name }}</p>
                <button class="preview-remove" @click="removeFile"></button>
              </div>
              <!--  -->
              <div class="preview-content">
                <!--  -->
                <img
                  v-if="isImage(selectedFile)"
                  :src="filePreviewUrl"
                  alt="file preview"
                />
                <!-- PDF  -->
                <embed
                  v-else-if="isPDF(selectedFile)"
                  :src="filePreviewUrl"
                  type="application/pdf"
                  class="pdf-preview"
                />
                <!-- DOC/DOCX  -->
                <div v-else class="doc-preview">
                  <i class="doc-icon"></i>
                  <p></p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!--  -->
        <div class="modal-actions">
          <button class="btn btn-white" @click="closeModal"></button>
          <button
            class="btn btn-primary"
            :disabled="!selectedFile"
            @click="confirmImport"
          >
            
          </button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'ImportResumeModal',
  data() {
    return {
      selectedFile: null,
      filePreviewUrl: null
    }
  },
  methods: {
    // 
    closeModal() {
      this.$emit('close')
    },
    // 
    triggerFileInput() {
      this.$refs.fileInput.click()
    },
    // 
    onFileChange(e) {
      const file = e.target.files[0]
      this.handleFile(file)
    },
    // 
    onDragOver(e) {
      e.currentTarget.classList.add('drag-over')
    },
    // 
    onDragLeave(e) {
      e.currentTarget.classList.remove('drag-over')
    },
    // 
    onDropFiles(e) {
      e.currentTarget.classList.remove('drag-over')
      if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
        const file = e.dataTransfer.files[0]
        this.handleFile(file)
      }
    },
    // 
    handleFile(file) {
      if (!file) return
      // 
      const validExtensions = ['pdf', 'doc', 'docx', 'png']
      const ext = file.name.split('.').pop().toLowerCase()

      if (!validExtensions.includes(ext)) {
        alert(' PDF / DOC / DOCX / PNG')
        return
      }

      this.selectedFile = file
      //  PDF URL
      if (ext === 'png' || ext === 'pdf') {
        this.filePreviewUrl = URL.createObjectURL(file)
      } else {
        this.filePreviewUrl = null
      }
    },
    // 
    removeFile() {
      this.selectedFile = null
      this.filePreviewUrl = null
      //  input
      this.$refs.fileInput.value = ''
    },
    // 
    confirmImport() {
      // 
      this.$emit('confirmImport', this.selectedFile)
    },
    // 
    isImage(file) {
      return file && file.type.includes('image')
    },
    //  PDF
    isPDF(file) {
      return file && file.type === 'application/pdf'
    }
  }
}
</script>

